import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  model: {
    prop: 'visible',
    event: 'closeWindow'
  },
  data () {
    return {
      dsToIndex: -1,
      dsGiftIndex: -1,
      swiperDsPersonOption: {
        slidesPerView: 4,
        freeMode: true,
        pagination: {
          el: '.swiper-pagination'
        }
      },
      swiperDsGiftOption: {
        slidesPerColumn: 2,
        slidesPerView: 4,
        freeMode: true,
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  },
  props: ['visible'],
  methods: {
    closeWindow () {
      this.$emit('closeWindow', false)
    }
  },
  components: {
    swiper,
    swiperSlide
  }

}



