import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import Upload from '../Upload'
export default {
  model: {
    prop: 'visible',
    event: 'closeWindow'
  },
  props: ['visible'],
  data () {
    return {
      swiperThemeOption: {
        slidesPerColumn: 2,
        slidesPerView: 4,
        freeMode: true,
        pagination: {
          el: '.swiper-pagination'
        }
      },
      base64Img: ''
    }
  },
  methods: {
    closeWindow () {
      this.$emit('closeWindow', false)
    },
    afterClip (base64) {
      this.base64Img = base64
    }
  },
  components: {
    Upload,
    swiper,
    swiperSlide
  }

}



